.button {
  width: 200px;
  height: 50px;
  background-color: bisque;
  font-size: 20px;
  font-weight: 600;
  line-height: 22px;
  margin: 0;
  padding: 0;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
  &:hover {
    transform: scale(1.05);
  }
}
